<template>
  <div
    class="flex items-center justify-center h-screen w-screen overflow-hidden"
  >
    <div class="text-center mx-20">
      <lottie-player
        class="mx-auto"
        src="https://assets10.lottiefiles.com/packages/lf20_sonmjuir.json"
        background="transparent"
        speed="1"
        style="width: 250px; height: 250px"
        loop
        autoplay
      ></lottie-player>
      <h2 class="text-4xl font-semibold my-2">
        Congratulations! You are all set.
      </h2>
      <p class="my-2">Now you are ready to start managing your dashboard.</p>
      <template v-if="countdown > 0">
        <small>You will be redirected in {{ countdown }} seconds</small>
      </template>
      <template v-else>
        <small>If the page does not redirect automatically, you can click the button below.</small>
      </template>
      <div
        v-if="showButton"
        class="mt-10">
        <Button
          color="teal"
          @click="redirect">
          Go to dashboard
          <i class="material-icons ml-2">east</i>
        </Button>
      </div>
    </div>
  </div>
</template>
<script>
import { defineComponent, onMounted, ref } from 'vue'
import { useRouter } from 'vue-router' 

import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  components: {
    Button
  },
  setup() {
    const router = useRouter()
    const countdown = ref(5)
    const showButton = ref(false)
    const interval = ref()

    const redirect = () => {
      clearInterval(interval.value)
      router.push({
        path: '/dashboard'
      })
    }

    onMounted(async () => {
      countdown.value = 5
      showButton.value = false
      interval.value = setInterval(() => {
        if (countdown.value > 0) {
          countdown.value--
        } else {
          clearInterval(interval)

          try {
            redirect()
          } catch (e) {
            showButton.value = true
          }
          showButton.value = true
        }
      }, 1000)
    })
    return {
      countdown,
      showButton,
      redirect
    }
  }
})
</script>
